export default {
	data() {
		return {
			listData: [],
			pageTotal:1,
			pageSize:10,
			pageIndex:1,
			nodata:false,
			nomore:false,
			type:1
		}
	},
	onLoad(options) {
		this.type= options.type;
		if(this.type=='1'){
			uni.setNavigationBarTitle({
				title: '我的关注'
			});
		}else{
			uni.setNavigationBarTitle({
				title: '关注我的'
			});
		}
		
	},
	onShow(){
		this.initData()
	},
	onReachBottom(){
		if(this.pageIndex<this.pageTotal){
			this.pageIndex++
			this.initData()
		}else{
			this.nomore=true
		}
	},
	methods: {
		initData() {
			let self = this
			let params={
				pageSize:this.pageSize,
				pageIndex:this.pageIndex,
				fromType:self.type=='1'?true:false
			}
			self.$acFrame.HttpService.followListOwner(params).then(res => {
				if (res.success) {
					let list = res.data.rows
					self.pageTotal = res.data.pageTotal
					if(list.length>0){
						list.filter(v=>{
						  v.imgHeadPath = self.$acFrame.Util.setImgUrl(v.imgHeadPath);
						})
						self.listData=self.listData.concat(list)
					}else{
						self.nodata=true
					}
					
				}
			})
		},
		followUser(code,ind){ // 
			let self=this
			self.$acFrame.HttpService.followPost({userCode:code}).then(res => {
				if (res.success) {
					if(res.data){
						self.$acFrame.Util.mytotal('关注成功！')
					}else{
						self.$acFrame.Util.mytotal('已取消关注！')
					}
					self.reGet()
				}
			})
		},
		reGet(){
			this.pageIndex = 1
			this.pageSize = 10
			this.nodata=false
			this.nomore=false
			this.listData=[]
			this.initData()
		}
	}
}
